import React, { Component } from 'react';
import {withGetScreen} from 'react-getscreen'
import MenuCentral from './menuCentral.js'
import LinksFooter from './linksFooter.js'
import './styles/contraloriaSocial.css'






class ContraloriaSocial extends Component{

    constructor(props){
      super(props);
      this.state={
        carrerasArray:null,
      }

    }






  render(){
    return(
      <div className="div-principal-contraloriaSocial">

      <MenuCentral/>

      <div className="div-contraloriaSocial">

        <img src="http://tecmm.edu.mx/imagesReact/images/contraloriaSocial/BotonContraloria.jpg"/>

        <div className="div-documentosContraloria">

          <a href="http://tecmm.edu.mx/documentos/contraloriaSocial/Guia-Contraloria-Social.pdf">
            <button className="button-enlaces">GUA OPERATIVA DE CONTRALORA SOCIAL</button>
          </a>

          <a href="http://tecmm.edu.mx/documentos/contraloriaSocial/proceso-quejas.pdf">
            <button className="button-enlaces">PROCEDIMIENTO PARA QUEJAS, DENUNCIAS O PETICIONES</button>
          </a>

          <a href="http://tecmm.edu.mx/documentos/contraloriaSocial/formato-quejas,denuncias,sugerencias-contraloria-social.pdf">
            <button className="button-enlaces">QUEJAS, DENUNCIAS O PETICIONES</button>
          </a>

          <a href="http://tecmm.edu.mx/documentos/contraloriaSocial/que-es-prodep.pdf">
            <button className="button-enlaces">QU ES EL PRODEP?</button>
          </a>

          <a href="http://tecmm.edu.mx/documentos/contraloriaSocial/esquema-contraloria-social.pdf">
            <button className="button-enlaces">ESQUEMA DE CONTRALORIA SOCIAL</button>
          </a>

          <a href="http://tecmm.edu.mx/documentos/contraloriaSocial/programa-anual.pdf">
            <button className="button-enlaces">PROGRAMA ANUAL 2020</button>
          </a>

          <a href="http://tecmm.edu.mx/documentos/contraloriaSocial/lineamientos-contraloria-social.pdf">
            <button className="button-enlaces">LINEAMIENTOS DE CONTRALORIA SOCIAL</button>
          </a>

          <a href="https://dpii.acad-tecnm.mx/prodep/">
            <button className="button-enlaces">ENLACE<br/>TECNM<br/><br/></button>
          </a>

          <a href="http://tecmm.edu.mx/documentos/contraloriaSocial/Manual-2022.pdf">
            <button className="button-enlaces">MANUAL DE USUARIO<br/><br/></button>
          </a>

        </div>



        <div className="div-pregunta">
          <h1>Qu es la Contralora Social?</h1>
          <p>La Contralora Social es un grupo de beneficiarios, que, de manera organizada, verificaran el cumplimiento de las metas y la correcta aplicacin de los recursos pblicos asignados al Programa PRODEP.</p>
        </div>

        <div className="div-pregunta">
          <h1>Qu vigila la contralora Social?</h1>
            <p>
            <ul>
              <li>Que se difunda la informacin suficiente, veraz y oportuna sobre la operacin del programa.</li>
              <li>Que los otorgamientos de los apoyos sean con calidad, calidez, eficiente, eficaz, oportuno y de manera transparente.</li>
              <li>Que las autoridades competentes brinden atencin a las quejas o denuncias, relacionadas al programa.</li>
            </ul>
            </p>

        </div>

        <div className="div-pregunta">
          <h1>Qu es un Comit de Contralora Social?</h1>
          <p>Las formas de organizacin social constituidas por los beneficiarios referidas en el artculo 67 del Reglamento de la Ley General de Desarrollo Social, que llevan a cabo el seguimiento, supervisin y vigilancia de la ejecucin, cumplimiento de las metas y acciones comprometidas, as como de la correcta aplicacin de los recursos asignados a los mismos.</p>
        </div>

        <div className="div-pregunta">
          <h1>Objetivo de los Comits de Contralora Social.</h1>
          <p>Colaborar en forma VOLUNTARIA ACTIVA y de manera representativa con organismos oficiales, seguimiento, supervisin y vigilancia de la ejecucin, cumplimiento de las metas y acciones comprometidas en el Programa, as como de la correcta aplicacin de los recursos asignados.</p>

        </div>


        <div className="div-pregunta">
          <h1>Cules son las funciones que debern de cumplir los Comits de Contralora Social?</h1>
          <p>
            <ul>
              <li>Representar los intereses y actuar como grupo de enlace entre la comunidad, las autoridades locales y autoridades centrales.</li>
              <li>Vigilar el adecuado manejo de los recursos financieros y que cumplan con el objetivo para el que fueron otorgados.</li>
              <li>Capturar y canalizar las Quejas o Denuncias a las reas correspondientes.</li>
              <li>Reportar cualquier anomala que se presente durante el proceso de la ejecucin y culminacin de los recursos Financieros.</li>
            </ul>
          </p>

        </div>

        <div className="div-pregunta">
          <h1>Medidas para promover la equidad de gnero entre hombres y mujeres en la integracin de los Comits.</h1>
          <p>Dentro del Programa para el Desarrollo Profesional Docente (PRODEP) mismo que se rige por Reglas de operaciones emitidas por la Secretara de Hacienda y Crdito Pblico, donde menciona en el objetivo para el Tipo superior, Profesionalizar a las/los PTC, ofreciendo las mismas oportunidades a mujeres y hombres para acceder a los apoyos que otorga el Programa, a fin de que alcancen las capacidades de investigacin-docencia, desarrollo tecnolgico e innovacin y con responsabilidad social</p>
          <p>Por lo anterior, podemos mencionar que para la formacin de los comits debern de ser de manera equitativa.</p>
        </div>




      </div>


      <LinksFooter/>

      </div>
    );
  }
}


const options = {mobileLimit: 420, tabletLimit: 770}
export default withGetScreen(ContraloriaSocial, options);
