import React,{Component} from 'react';
import Fade from 'react-reveal/Fade';

import "./styles/posgrados.css";

class Posgrados extends Component{

  render(){
    return(
      <div>
        <div className="posgradosParallax1">
          <div className="mensajePosgrado">
            <h1>Posgrados TecMM</h1>
            <p>Como estudiante de posgrado en Tecnolgico Mario Molina Campus Zapopan, usted encontrar un gran ambiente de aprendizaje y profesionales dedicados a la excelencia en educacin. Contine leyendo para conocer nuestros programas y contctenos en caso de que tenga alguna pregunta.</p>
          </div>
        </div>

        <Fade left>
          <div className="queOfrecemosContainer">
            <h1>Programas Disponibles</h1>
            <h2>Que Ofrecemos?</h2>
          </div>
        </Fade>

        <div className="posgradosParallax2">
          <div className="infoMaestriaSistemas">
            <h1>Maestria en Sistemas Computacionales</h1>
            <p>Formar especialistas de manera integral con capacidad analtica, crtica, creativa y de liderazgo que aporten soluciones computacionales a las organizaciones.</p>
            <a href="/maestriaSistemas">
              Mas Informacin <i class="arrow right icon"></i>
            </a>
          </div>

          <div className="infoMaestriaAdministracion">
            <h1>Maestria en Administracin</h1>
            <p>La Maestra en Administracin, es una opcin que brindar a los profesionistas oportunidades de profundizar en la disciplina de la administracin, identificando las nuevas aportaciones de la ciencia y la tecnologa.</p>
            <a href="/maestriaAdministracion">
              Mas Informacin <i class="arrow right icon"></i>
            </a>
          </div>
        </div>






      </div>
    );
  }
}
export default Posgrados;
